






















































































































import Vue from "vue";
import { firebaseAuth } from "@/main";

export default Vue.extend({
  name: "Login",

  data: () => ({
    email: "",
    password: "",
    error: "",
    loggedIn: false,
  }),
  created() {
    firebaseAuth.onAuthStateChanged(firebaseAuth.getAuth(), (user) => {
      this.loggedIn = !!user;
    });
    console.log("created! = ", this.loggedIn);
  },
  methods: {
    async login() {
      try {
        const user = await firebaseAuth.signInWithEmailAndPassword(
          firebaseAuth.getAuth(),
          this.email,
          this.password
        );
        console.log("logged in as: ", user);
        this.loggedIn = true;
        await this.$router.replace({ name: "InitGame" });
      } catch (err) {
        console.log(err);
        this.error = String(err);
      }
    },
    async registerWithGoogle() {
      try {
        let provider = new firebaseAuth.GoogleAuthProvider();
        const user = await firebaseAuth.signInWithPopup(
          firebaseAuth.getAuth(),
          provider
        );
        console.log("logged in as: ", user);
        await this.$router.replace({ name: "InitGame" });
      } catch (err) {
        console.log(err);
        this.error = String(err);
      }
    },
    async signout() {
      try {
        const user = await firebaseAuth.signOut(firebaseAuth.getAuth());
        console.log(user);
        // await this.$router.replace({ name: "Login" });
        this.loggedIn = false;
      } catch (err) {
        console.log(err);
        this.error = String(err);
      }
    },

    async register() {
      try {
        const user = await firebaseAuth.createUserWithEmailAndPassword(
          firebaseAuth.getAuth(),
          this.email,
          this.password
        );
        console.log(user);
        this.loggedIn = true;
        this.$router.replace({ name: "InitGame" });
      } catch (err) {
        console.log(err);
        this.error = String(err);
      }
    },
  },
});
